"use client";

import { Suspense } from "react";
import type { ComponentType } from "react";
import Link from "next/link";
import { useSearchParams } from "next/navigation";
import { Loader2, BarChart3, TrendingUp, TrendingDown } from "lucide-react";
import { useBacktest, useBacktests } from "@/lib/hooks";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { cn, formatDate, formatNumber, formatPercent } from "@/lib/utils";

export default function ResultsPage() {
  return (
    <Suspense
      fallback={
        <div className="flex h-full items-center justify-center">
          <Loader2 className="h-8 w-8 animate-spin text-primary" />
        </div>
      }
    >
      <ResultsContent />
    </Suspense>
  );
}

function ResultsContent() {
  const searchParams = useSearchParams();
  const backtestId = searchParams.get("id");
  const { data: listData, isLoading: isListLoading } = useBacktests();
  const { data, isLoading } = useBacktest(backtestId);

  if (!backtestId) {
    const completed = (listData?.items || []).filter((item) => item.status === "COMPLETED");
    return (
      <div className="p-6">
        <div className="mb-6">
          <h2 className="text-xl font-semibold text-foreground">结果分析</h2>
          <p className="mt-1 text-sm text-muted-foreground">请选择一个已完成的回测任务查看结果</p>
        </div>
        {isListLoading ? (
          <div className="flex h-48 items-center justify-center">
            <Loader2 className="h-7 w-7 animate-spin text-primary" />
          </div>
        ) : completed.length > 0 ? (
          <div className="grid gap-3">
            {completed.map((item) => (
              <Link key={item.id} href={`/results?id=${item.id}`}>
                <Card className="transition-colors hover:border-primary/50">
                  <CardContent className="py-4">
                    <div className="flex items-center justify-between gap-3">
                      <div className="min-w-0">
                        <p className="truncate text-sm font-medium text-foreground">{item.name}</p>
                        <p className="mt-1 text-xs text-muted-foreground">
                          {formatDate(item.start_date)} ~ {formatDate(item.end_date)}
                        </p>
                      </div>
                      <Badge variant="secondary">查看结果</Badge>
                    </div>
                  </CardContent>
                </Card>
              </Link>
            ))}
          </div>
        ) : (
          <Card>
            <CardContent className="py-12 text-center">
              <p className="text-sm text-muted-foreground">暂无已完成回测任务</p>
            </CardContent>
          </Card>
        )}
      </div>
    );
  }

  if (isLoading || !data) {
    return (
      <div className="flex h-full items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    );
  }

  if (data.status !== "COMPLETED") {
    return (
      <div className="p-6">
        <Card>
          <CardContent className="py-12 text-center">
            <p className="text-sm text-muted-foreground">
              该任务当前状态为 <span className="font-semibold text-foreground">{data.status}</span>，完成后可查看结果
            </p>
            <Link href={`/tasks?active=${data.id}`} className="mt-4 inline-block text-sm text-primary hover:underline">
              前往任务详情
            </Link>
          </CardContent>
        </Card>
      </div>
    );
  }

  const metrics = data.metrics || {};
  const totalReturn = Number(metrics.total_return_pct || 0);
  const maxDrawdown = Number(metrics.max_drawdown_pct || 0);
  const winRate = Number(metrics.win_rate_pct || 0);
  const sharpe = Number(metrics.sharpe_ratio || 0);
  const finalValue = Number(metrics.final_value || 0);
  const score = Number(metrics.score || 0);

  const bestStock = data.result?.best_stock;

  return (
    <div className="p-6">
      <div className="mb-6 flex items-start justify-between gap-3">
        <div>
          <h2 className="text-xl font-semibold text-foreground">结果分析</h2>
          <p className="mt-1 text-sm text-muted-foreground">
            {data.name} · {formatDate(data.start_date)} ~ {formatDate(data.end_date)}
          </p>
        </div>
        <Link href={`/tasks?active=${data.id}`} className="text-sm text-primary hover:underline">
          返回任务详情
        </Link>
      </div>

      <div className="grid gap-3 sm:grid-cols-2 lg:grid-cols-3">
        <MetricCard
          label="总收益率"
          value={formatPercent(totalReturn)}
          positive={totalReturn >= 0}
          icon={totalReturn >= 0 ? TrendingUp : TrendingDown}
        />
        <MetricCard
          label="最大回撤"
          value={formatPercent(maxDrawdown)}
          positive={false}
          icon={TrendingDown}
        />
        <MetricCard label="胜率" value={formatPercent(winRate)} positive={winRate >= 50} icon={BarChart3} />
        <MetricCard label="夏普比率" value={sharpe.toFixed(2)} positive={sharpe >= 0} icon={BarChart3} />
        <MetricCard label="最终净值" value={formatNumber(finalValue)} positive={finalValue >= 0} icon={BarChart3} />
        <MetricCard label="策略评分" value={score.toFixed(2)} positive={score >= 5} icon={BarChart3} />
      </div>

      <div className="mt-4 grid gap-4 lg:grid-cols-2">
        <Card>
          <CardHeader>
            <CardTitle>最佳个股</CardTitle>
          </CardHeader>
          <CardContent>
            {bestStock ? (
              <div className="space-y-1">
                <p className="text-sm text-muted-foreground">代码</p>
                <p className="font-mono text-base font-semibold text-foreground">{bestStock.code}</p>
                <p
                  className={cn(
                    "text-sm font-medium",
                    Number(bestStock.net_pnl) >= 0 ? "text-[hsl(var(--profit))]" : "text-[hsl(var(--loss))]"
                  )}
                >
                  净利润: {formatNumber(Number(bestStock.net_pnl))}
                </p>
              </div>
            ) : (
              <p className="text-sm text-muted-foreground">暂无数据</p>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>任务信息</CardTitle>
          </CardHeader>
          <CardContent className="space-y-2 text-sm">
            <InfoRow label="任务 ID" value={`${data.id.slice(0, 8)}...`} />
            <InfoRow label="创建时间" value={formatDate(data.created_at)} />
            <InfoRow label="完成时间" value={data.finished_at ? formatDate(data.finished_at) : "-"} />
            <InfoRow label="状态" value={data.status} />
          </CardContent>
        </Card>
      </div>
    </div>
  );
}

function MetricCard({
  label,
  value,
  positive,
  icon: Icon,
}: {
  label: string;
  value: string;
  positive: boolean;
  icon: ComponentType<{ className?: string }>;
}) {
  return (
    <Card>
      <CardContent className="py-4">
        <div className="flex items-center justify-between">
          <p className="text-xs text-muted-foreground">{label}</p>
          <Icon className="h-4 w-4 text-muted-foreground" />
        </div>
        <p
          className={cn(
            "mt-2 text-xl font-semibold font-mono",
            positive ? "text-[hsl(var(--profit))]" : "text-[hsl(var(--loss))]"
          )}
        >
          {value}
        </p>
      </CardContent>
    </Card>
  );
}

function InfoRow({ label, value }: { label: string; value: string }) {
  return (
    <div className="flex items-center justify-between gap-3">
      <span className="text-muted-foreground">{label}</span>
      <span className="font-medium text-foreground">{value}</span>
    </div>
  );
}
